export default {
	data() {
		return {
			userInfo:{},
		};
	},
	onShow() {
		var me = this;
		var userInfo = uni.getStorageSync("globalUser");
			me.userInfo = userInfo;
	},
	methods: {
		operator(){
			var me = this;
			var globalUser = me.userInfo;
			uni.showActionSheet({
				itemList:["查看我的头像","从相册选择上传"],
				success(res) {
					var index = res.tapIndex;
					if(index == 0){
						//预览头像
						var faceArr = [];
						faceArr.push(globalUser.faceImage);
						uni.previewImage({
							urls:faceArr,
							current:faceArr[0]
						})
					}else if(index ==1){
						//选择上传头像
						uni.chooseImage({
							count:1,
							sizeType:["compressed"],
							sourceType:["album"],
							success(res) {
								var tempFliePath = res.tempFliePaths[0];
								uni.navigateTo({
									url:"../meFace/meFace?tempFilePath=" + tempFliePath
								})
							}
						})
						
					}
				}
			})
		},
		modifyNickname(){
			uni.navigateTo({
				url:"../meNickname/meNickname"
			})
		},
		modifySex(){
			uni.navigateTo({
				url:"../sex/sex"
			})
		}
	}
}
